.contact-form {
    padding-top: 50px;
}

label {
    display: block;
    font-size: 14px;
    font-weight: 500;
    color: #333333;
    text-align: left;
}

.form-control {
    // border: 0px;
    // border-radius: 0px;
    // margin-top: 10px;
    background: transparent;
    // margin-bottom: 40px;
    // border-bottom: 1px solid #999999;
    // -webkit-box-shadow: inset 0 0px 0px rgba(0, 0, 0, 0);
    // box-shadow: inset 0 0px 0px rgba(0, 0, 0, 0);
    // -webkit-transition: 0.5s;
    // -moz-transition: 0.5s;
    // transition: 0.5s;
    padding: 9px 15px;
}

.form-control:focus {
    border-color: #04a9f5;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;
}

.form-control::-webkit-input-placeholder {
    font-size: 14px;
    color: #999999;
}

.form-control:-moz-placeholder {
    font-size: 14px;
    color: #999999;
}

.form-control::-moz-placeholder {
    font-size: 14px;
    color: #999999;
}

.form-control:-ms-input-placeholder {
    font-size: 14px;
    color: #999999;
}

.text-muted {
    font-size: 13px;
    color: #222;
}

.text-muted strong {
    color: #FF3333;
}

.btn-send {
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    margin-bottom: 20px;
    border-radius: 0px;
